      if (e.target.type === 'checkbox' && !e.target.checked){
         if(ids ==='Male'){ setpropertyname({...ispropertyname,pgboys:null});}
         if(ids ==='Female'){ setpropertyname({ ...ispropertyname,pggirls:null}); }
         if(ids ==='privateroom'){ setpropertyname({ ...ispropertyname,privateroom:null}); }
         if(ids ==='tworoom'){ setpropertyname({ ...ispropertyname,tworoom:null}); }
         if(ids ==='twoplusroom'){ setpropertyname({ ...ispropertyname,twoplusroom:null}); }

    } else{
      if(ids ==='Male'){  setpropertyname({...ispropertyname, pgboys:ids }); }
      if(ids ==='Female'){ setpropertyname({ ...ispropertyname, pggirls:ids }); }
      if(ids ==='privateroom'){ setpropertyname({ ...ispropertyname, privateroom:ids }); }
      if(ids ==='tworoom'){ setpropertyname({ ...ispropertyname, tworoom:ids }); }
      if(ids ==='twoplusroom'){ setpropertyname({ ...ispropertyname, twoplusroom:ids }); }
     
      
    }
